<template>
  <div class="myOrder">
    <div class="myOrderHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.push('/personalCenter')" />
        <span>{{ $t("index.text66") }}</span>
      </div>
    </div>
    <div class="slot"></div>

    <div class="myOrderMain">
      <van-tabs
        @click="changeTab"
        v-model="status"
        line-width="2rem"
        background="#fffbf1"
      >
        <van-tab :title="$t('index.text111')" :name="0">
          <OrderList :type="0" :orderInfoList="orderInfoList"></OrderList>
        </van-tab>
        <van-tab :title="$t('index.text112')" :name="1">
          <OrderList :type="0" :orderInfoList="orderInfoList"></OrderList>
        </van-tab>
        <van-tab :title="$t('index.text113')" :name="4">
          <OrderList :type="0" :orderInfoList="orderInfoList"></OrderList
        ></van-tab>
        <van-tab :title="$t('index.text114')" :name="2">
          <OrderList :type="0" :orderInfoList="orderInfoList"></OrderList
        ></van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import OrderList from "../merchantCenter/components/orderList";
export default {
  components: {
    OrderList
  },
  data() {
    return {
      selectQuantity: 1,
      status: 0,
      type: 0,
      orderInfoList: []
    };
  },
  async activated() {
    if (this.status == 4) {
      this.getEvaluateList();
    } else {
      this.getList(this.status);
    }
  },
  methods: {
    getList(v) {
      this.$api
        .get("orders/status", {
          status: v,
          type: 0
        })
        .then(res => {
          this.orderInfoList = res.data;
        });
    },

    // 未评价列表
    getEvaluateList() {
      this.$api.get("orders/evaluate", {}).then(res => {
        this.orderInfoList = res.data;
      });
    },

    changeTab(v) {
      if (v != 4) {
        this.getList(v);
      } else {
        this.getEvaluateList();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.myOrder {
  height: 100%;
  overflow: scroll;
  background: #fffbf1;
}
.myOrderHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}
.myOrderMainHeader {
  background: #bd303e;
  height: 37.5rem;
}
.myOrderMain {
  padding: 0 1.6rem 1.6rem 1.6rem;

  .orderList {
    .orderItem {
      margin-top: 1.4rem;
      padding: 1.4rem;
      background: #fff;
      .orderNum {
        font-size: 1.2rem;
      }

      .orderContent {
        margin: 1.4rem 0;
        padding: 1rem;
        background: #f7f7f7;
        display: flex;
        .left {
          width: 8.5rem;
          height: 8.5rem;
          background: #bd303e;
        }

        .right {
          margin-left: 2rem;
          flex: 1;
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          .textInfo {
            font-weight: bold;
            font-size: 1.2rem;
            line-height: 2.5rem;
          }
          .price {
            font-size: 1.8rem;
            color: #d12c21;
            font-weight: bold;
          }
        }
      }

      .line {
        background: #cccccc;
        height: 0.1rem;
      }

      .btnBox {
        .left {
          color: #d12c21;
          font-weight: bold;
        }
        .right {
          padding: 1rem 1.5rem;
          height: 3rem;
          background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
          border-radius: 1.5rem;
          color: #fff;
        }
      }
    }
  }
}
</style>



